import axios from "axios";
import { INewLink } from "../types/types";

export async function addLink(link: INewLink) {
  const response = await axios.post("https://url-bakc.onrender.com", link);
  return response;
}

export async function getStatistics(id: string) {
  const response = await axios.get(`https://url-bakc.onrender.com/info/${id}`);
  return response;
}
